html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  overflow: hidden;

  background: #c0392b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #8e44ad, #c0392b);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#8e44ad), to(#c0392b));
  background: -webkit-linear-gradient(top, #8e44ad, #c0392b);
  background: -o-linear-gradient(top, #8e44ad, #c0392b);
  background: linear-gradient(to bottom, #8e44ad, #c0392b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}



